// because this is a recact-component, we must import React
import React from 'react'

const Note = ({ note, toggleImportance }) => {
    const label = note.important
      ? 'make not important' : 'make important'
    // if important have button say make not important
    // if not important have button say make important
    return (
      <li className = 'note'>
        {note.content} 
        <button onClick={toggleImportance}>{label}</button>
      </li>
    // onClick will have a function that will execute
    )
  }

export default Note