import React, {useState, useEffect} from 'react'
import Note from './components/Note'
import noteService from './services/notes'
import Notification from './components/Notification'
import Footer from './components/Footer'
// part 2d notes
const App = (props) => {
    const [notes, setNotes] = useState(props.notes)
    const [newNote, setNewNote] = useState(
        'a new note...'
      ) 
    const [showAll, setShowAll] = useState(true)
    const [errorMessage, setErrorMessage] = useState('')
    // ternary operator
    const notesToShow = showAll
      ? notes
      : notes.filter(note => note.important === true) // if true shoe all notes
      // false show the notes that are important (important === true)
    // could just use note.important since we dont really need the === true
    // because note.imrportant is bool itself
    useEffect(() => {
      noteService      
      .getAll()      
      .then(initialNotes => {        
        setNotes(initialNotes)   
      })  
    }, [])
    const toggleImportanceOf = id => {
      const note = notes.find(n => n.id === id)
      const changedNote = { ...note, important: !note.important }
  
      noteService
        .update(changedNote).then(returnedNote => {
          setNotes(notes.map(note => note.id !== id ? note : returnedNote))
        })
        .catch(error => {
          setErrorMessage(          
            `Note '${note.content}' was already removed from server`
            )        
            setTimeout(() => {          
              setErrorMessage(null)        }, 
              5000)        
            setNotes(notes.filter(n => n.id !== id))
        })
    }
    // first line defines the unique url for each note resrouce
    // based on its id. 

    // the array find method is used to find the ntoe we want
    // to modify, and we then assign it to the note variable

    // third lne feautres a object spread which hasnt been
    // standardized as a part of JS language specs
    // inpractive {..note} creates a new object with copies all 
    // properties of OG note, then we add a change to important,
    // which would be important: true. changedNote
    // is a shallow copy!, meaning values of new
    // object are same as old ones
    
    // then we use PUT request to backend where is will replace
    // the ol object with map. for each note if note.id is not 
    // the id we are looking for use the original note, if it is
    // then replace it with the note we have just created.
    const rows = () => notesToShow.map(note =>
    <Note
      key={note.id}
      note={note}
      toggleImportance={() => toggleImportanceOf(note.id)}    />
    )

    const addNote = (event) => {
      event.preventDefault()
      const noteObject = {
        content: newNote,
        date: new Date().toISOString(),
        important: Math.random() > 0.5
      }
  
      noteService      
      .create(noteObject)      
      .then(returnedNote => {        
        setNotes(notes.concat(returnedNote))     
        setNewNote('')      
      })  
    }
  
    // concat does not chage the components OG state but return 
    // a new copy of the list


    // The event handler is called every time a change occurs 
    // in the input element. The event handler function receives 
    // the event object as its event parameter:
    const handleNoteChange = (event) => {
        console.log(event.target.value)
        setNewNote(event.target.value)
      }
    // button setSHowAll makes it so that we can revert form important
    // === true and those that are important and are not important
    return (
      <div>
        <h1>Notes</h1>
        <Notification class = "error" message={errorMessage} />
        <div>
            <button onClick={() => setShowAll(!showAll)}> 
            show {showAll ? 'important' : 'all' }
            </button>
        </div>
        <ul>
          {rows()}
        </ul>
        <form onSubmit={addNote}>
            <input 
                value = {newNote}
                onChange = {handleNoteChange}
            />
            <button type="submit">save</button>
        </form>
        <Footer />
      </div>
    )
  }
export default App

